/* Header styling  */
.section-header-parent {
  display: flex;
}

.section-header-child {
  text-align: left;
  padding: 30px 0px;
}

.section-header-h1 {
  padding-right: 15px;
  padding-left: 10px;
  width: 25%;
}

.list-number {
  width: 60px;
  color: #00909e
}

.after-header {
  border: 4px solid white;
  border-radius: 5px;
  width: 400px;
  padding-left: 10px;
  margin-bottom: .5rem;
  font-weight: 500;
  line-height: 1.2;
}